// app/auth/signup/page.tsx
"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import useSignup from "../../hooks/auth/useSignup";
import { isValidEmail } from "../../hooks/auth/authUtils";

const SignupPage = () => {
  const [name, setName] = useState(""); // State for Name input
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const router = useRouter();
  const { signup, isLoading } = useSignup();

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault();

    // Removed toast-based validation, you can keep console.log for now,
    // or implement inline error messages below input fields

    if (!name.trim()) {
      console.log("Error: Name is required"); // Example: Replace with inline error display
      return;
    }
    if (!email) {
      console.log("Error: Email is required"); // Example: Replace with inline error display
      return;
    }
    if (!isValidEmail(email)) {
      console.log("Error: Invalid email format"); // Example: Replace with inline error display
      return;
    }
    if (!password) {
      console.log("Error: Password is required"); // Example: Replace with inline error display
      return;
    }
    if (password.length < 6) {
      console.log("Error: Password must be at least 6 characters"); // Example: Replace with inline error display
      return;
    }

    try {
      await signup(email, password, name); // Pass name to signup hook
      // Removed success toast:
      // showToast("Success", "Account created! Please login.", "success");
      console.log("Success: Account created! Please login."); // Example: Replace with inline success message or redirection
      router.push("/service/color-process/");
    } catch (error) {
      console.error("Signup error:", error);
      // Removed error toast:
      // showToast("Error", "Signup failed. Please try again.", "error"); // Example: Replace with inline error display
      console.log("Error: Signup failed. Please try again."); // Example: Replace with inline error display
    }
  };

  // Inline styles for input classes
  const inputStyle = {
    appearance: "none",
    backgroundColor: "#202020",
    borderBottomColor: "#62D474", // Assuming primary color is #62D474
    borderBottomWidth: "1px",
    paddingTop: "0.5rem", // py-2 -> 0.5rem
    paddingBottom: "0.5rem",
    paddingLeft: "0.75rem", // px-3 -> 0.75rem
    paddingRight: "0.75rem",
    lineHeight: "tight",
    focusOutline: "none",
    boxShadow: "none", // Remove shadow-2xl class effect from input
    width: "100%", // w-full
  };

  // Inline styles for button classes
  const buttonStyle = {
    backgroundColor: "#62D474", // Assuming primary color is #62D474
    color: "white",
    fontWeight: "bold",
    paddingTop: "0.5rem", // py-2 -> 0.5rem
    paddingBottom: "0.5rem",
    paddingLeft: "1rem", // px-4 -> 1rem
    paddingRight: "1rem",
    borderRadius: "0.25rem", // rounded -> 0.25rem
    focusOutline: "none",
    focusShadow: "outline-none",
  };

  return (
    <div className="relative mx-auto max-w-md min-h-[100vh] top-16">
      <div className="p-6 shadow-2xl">
        <h2 className="text-2xl font-bold mb-6 text-center">Sign Up</h2>
        <form onSubmit={handleSignup} className="space-y-4">
          {/* --- Name Input --- */}
          <div>
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="name"
            >
              Name
            </label>
            <input
              style={inputStyle} // Apply inline style
              id="name"
              type="text"
              placeholder="Your Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </div>
          {/* --- Email Input --- */}
          <div>
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="email"
            >
              Email
            </label>
            <input
              style={inputStyle} // Apply inline style
              id="email"
              type="email"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          {/* --- Password Input --- */}
          <div>
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="password"
            >
              Password
            </label>
            <input
              style={inputStyle} // Apply inline style
              id="password"
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <button
            style={buttonStyle} // Apply inline style
            type="submit"
            disabled={isLoading}
          >
            {isLoading ? "Signing Up..." : "Sign Up"}
          </button>
        </form>
        <p className="text-center mt-4">
          Already have an account?{" "}
          <a
            href="/color-process/auth/login"
            className="text-blue-500 hover:underline"
          >
            Login
          </a>
        </p>
      </div>
    </div>
  );
};

export default SignupPage;
